.footer-nav {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  //width: 100%;
  max-width: 920px;

  @include vp-767 {
    align-items: flex-end;
    margin-top: -20px;
    margin-bottom: 33px;
  }
}

.footer-nav__list {
  //display: grid;
  //justify-content: end;
  //width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  align-self: flex-start;
  margin: 0;
  margin-right: 40px;
  padding: 0;

  list-style: none;
  //column-gap: 40px;
  //grid-template-columns: repeat(auto-fit, minmax(40px, max-content));

  @include vp-767 {
    //display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-right: 0;
  }
  //justify-items: end;
  //row-gap: 20px;

  &--right {
    //margin-right: 40px;
    @include vp-767 {
      align-self: flex-start;
      margin-right: 75px;
      padding-top: 42px;
    }
  }

  &--left {
    //justify-content: start;
    //margin-right: 40px;
    @include vp-767 {
      align-self: flex-start;
    }
  }
}
/*
.footer-nav__list:last-child {
  margin-right: 0;
}*/

.footer-nav__item {
  margin-right: 40px;

  @include vp-1199 {
    margin-right: 30px;
  }
  @include vp-767 {
    margin-right: 0;
    margin-bottom: 16px;
  }
}

.footer-nav__item:last-child {
  margin-right: 0;

  @include vp-767 {
    margin-bottom: 0;
  }
}

.footer-nav__item a {
  font-size: 14px;
  line-height: 16px;
  color: $color-default-white;

  @include vp-767 {
    font-size: 16px;
    line-height: 19px;
  }
}
